<template>
  <v-card class="login">
    <v-card-text>
      <v-container fluid>
        <!--<v-layout row>-->
        <!--<v-flex xs4>-->
        <!--<v-subheader>Navn</v-subheader>-->
        <!--</v-flex>-->
        <!--<v-flex xs8>-->
        <!--<v-text-field-->
        <!--name="name"-->
        <!--label="Skriv navnet ditt"-->
        <!--v-model="user.name"-->
        <!--&gt;-->

        <!--</v-text-field>-->
        <!--</v-flex>-->
        <!--</v-layout>-->
        <v-layout row>
          <v-flex xs4>
            <v-subheader>Epost</v-subheader>
          </v-flex>
          <v-flex xs8>
            <v-text-field
              name="email"
              label="Skriv epostadressen din"
              v-model="user.email"
            >

            </v-text-field>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs4>
            <v-subheader>Passord</v-subheader>
          </v-flex>
          <v-flex xs8>
            <v-text-field
              name="password"
              label="Skriv passordet ditt"
              v-model="user.password"
              min="8"
              :append-icon="v1 ? 'visibility' : 'visibility_off'"
              :append-icon-cb="() => v1 = !v1"
              :type="v1 ? 'password' : 'text'"
            >

            </v-text-field>
          </v-flex>
        </v-layout>
        <v-layout class="text-xs-center" row>
          <v-flex xs12>
            <v-btn @click="login($event)" color="primary">Log på</v-btn>
          </v-flex>
        </v-layout>
        <v-layout class="text-xs-center" row>
          <v-flex xs12>
            <v-btn flat color="primary" href="#/signup">Eller registrer deg her</v-btn>
            <!--<router-link class="v-btn" to="/login">Eller gå til log inn</router-link>-->
          </v-flex>
        </v-layout>
      </v-container>
    </v-card-text>
  </v-card>
</template>
<script>
  import firebase from 'firebase'

  export default {
    name: 'Login',
    data () {
      return {
        v1: true,
        user: {
          name: '',
          password: '',
          email: ''
        }
      }
    },
    methods: {
      login (event) {
        firebase.auth().signInWithEmailAndPassword(this.user.email, this.user.password)
          .then(
            user => {
              console.log(user)
              this.$router.replace('hello')
            },
            err => {
              console.error(err.message)
            }
          )
      }
    }
  }
</script>
<style>
  .login {
  }
</style>



