<template>
  <div class="list-test">

    <div class="list list1">
      <vue-draggable
        class="list-ul"
        :class="{'highlight': isDragging}"
        :move="onMove"
        @start="startDrag"
        @end="endDrag"
        v-model="items"
        :options="options"
        element="ul"
      >
        <transition-group>
          <li class="item" v-for="(item, idx) in items" :key="idx">{{ item }}</li>
        </transition-group>
      </vue-draggable>
    </div>
    <div class="list list2">
      <vue-draggable
        :move="onMove"
        class="list-ul"
        :class="{'highlight': isDragging}"
        @start="startDrag"
        @end="endDrag"
        v-model="items2"
        :options="{ group: 'tall' }">
        <transition-group>
          <div class="item li" v-for="(item, idx) in items2" :key="idx">{{ item }}</div>
        </transition-group>
      </vue-draggable>
      <div class="solid"><a href="#/hello">TEST</a></div>
    </div>

    <div class="list list3">
      <vue-draggable
        :move="onMove"
        class="list-ul"
        :class="{'highlight': isDragging}"
        @start="startDrag"
        @end="endDrag"
        v-model="items3"
        :options="{ group: 'tall' }">
        <transition-group>
          <div class="item li" v-for="(item, idx) in items3" :key="idx">{{ item }}</div>
        </transition-group>
      </vue-draggable>
      <div class="solid"><a href="#/hello">TEST</a></div>
    </div>
  </div>
</template>
<script>
  import VueDraggable from 'vuedraggable'

  export default {
    name: 'ListTest',
    components: {
      VueDraggable
    },
    methods: {
      startDrag (event) {
        this.isDragging = true
      },
      endDrag (event) {
        this.isDragging = false
      },
      onMove (event) {
//        console.log(event.relatedContext)
//        event.relatedContext.element.querySelector('span').style.backgroundColor = 'red'
//        this.isDragging = true
      }
    },
    data () {
      return {
        items: [],
        items2: [],
        items3: [],
        options: {
          group: 'tall',
          draggable: '.item',
          sort: false
        },
        isDragging: false
      }
    },
    created () {
      for (let i = 0; i < 10; i++) {
        this.items.push(i)
//        this.items2.push(i)
      }
    },
    mounted () {
      let el = this.$el
      console.log('ELEMENT', el)
      let lists = el.querySelectorAll('.list-ul span')
      console.log(lists)
      lists.forEach(list => {
        list.addEventListener('dragover', function (event) {
          event.target.classList.add('highlight')
        })
        list.addEventListener('dragend', function () {
          if (event.target.classList.contains('highlight')) {
            event.target.classList.remove('highlight')
          }
        })
        list.addEventListener('drop', function () {
          if (event.target.classList.contains('highlight')) {
            event.target.classList.remove('highlight')
          }
        })
        list.addEventListener('dragleave', function () {
          if (event.target.classList.contains('highlight')) {
            event.target.classList.remove('highlight')
          }
        })
      })
    }
  }
</script>
<style lang="scss">
  .list-ul {
    .highlight {
      background: #ffc400;
    }
  }

  /*.list-ul {*/
  /*&.highlight {*/
  /*span {*/
  /*!*background-color: rgba(255, 196, 0, 0.33);*!*/
  /*&:hover {*/
  /*background-color: black;*/
  /*}*/
  /*}*/
  /*}*/
  /*}*/

  .sortable-ghost {
    border: 2px solid green;
  }

  .list-test {
    width: 60vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    .list {
      width: 20%;
      min-width: 200px;
      margin: 10px;
    }

    .list1 ul {
      display: block;
      border: 1px solid red;
      min-height: 2.5em;
      /*padding: 2em;*/
      width: 20%;
      min-width: 180px;
      list-style: none;
      background: #7a171c;

      .item {
        display: block;
        margin: 3px;
        background: #69a2ad;
        padding: 5px 10px;
        &.li {

        }
        &.sortable-chosen {
          opacity: 0.4;
        }
      }
    }
    .list2,
    .list3 {
      overflow: hidden;
      border: 1px solid red;
      /*padding: 2em;*/
      width: 100%;
      min-width: 180px;
      list-style: none;
      background: #7a171c;
      position: relative;
      z-index: 0;
      .sortable-ghost {
        visibility: hidden;
      }
      a {
        display: inline-block;
        z-index: 12;
      }
      span {
        /*min-height: 1px;*/
        display: block;
        position: absolute;
        top: 30px;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 1;
      }
      div.item {
        height: 0;
        line-height: 0;
        padding: 0;
        margin: 0;
        visibility: hidden;
      }
      div.solid {
        position: absolute;
        padding: 5px;
        top: 0;
        width: 100%;
        text-align: center;
        background: #000;
        a {
          color: white;
        }
      }
    }
  }
</style>



