<template>
  <v-app dark>
    <v-navigation-drawer
      fixed
      :mini-variant="miniVariant"
      :clipped="clipped"
      v-model="drawer"
      app
    >
      <v-list>
        <v-list-tile
          v-for="(item, i) in items"
          :key="i"
          value="true"
          @click="goTo(item.route)"
        >
          <v-list-tile-action>
            <v-icon dark v-html="item.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
      <v-list>
        <v-list-tile
          v-for="(item, i) in menuItems"
          :key="i"
          value="true"
          @click="goTo(item.path)"
        >
          <v-list-tile-action>
            <v-icon v-if="item.meta.icon" dark v-html="item.meta.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.name"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-btn
        icon
        @click.stop="miniVariant = !miniVariant"
      >
        <v-icon v-html="miniVariant ? 'chevron_right' : 'chevron_left'"></v-icon>
      </v-btn>
      <v-btn
        icon
        @click.stop="clipped = !clipped"
      >
        <v-icon>web</v-icon>
      </v-btn>
      <v-btn
        icon
        @click.stop="fixed = !fixed"
      >
        <v-icon>remove</v-icon>
      </v-btn>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn
        icon
        @click.stop="rightDrawer = !rightDrawer"
      >
        <v-icon>menu</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content>
      <v-container fluid>
        <v-slide-y-transition mode="out-in">
          <v-layout column align-center>
            <!--<img src="/static/v.png" alt="Vuetify.js" class="mb-5"/>-->
            <!--<blockquote>-->
            <!--&#8220;First, solve the problem. Then, write the code.&#8221;-->
            <!--<footer>-->
            <!--<small>-->
            <!--<em>&mdash;John Johnson</em>-->
            <!--</small>-->
            <!--</footer>-->
            <!--</blockquote>-->
            <router-view></router-view>
          </v-layout>
        </v-slide-y-transition>
      </v-container>
    </v-content>
    <v-navigation-drawer
      temporary
      fixed
      :right="right"
      v-model="rightDrawer"
      app
    >
      <v-list>
        <v-list-tile @click.native="right = !right">
          <v-list-tile-action>
            <v-icon dark>compare_arrows</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>Switch drawer (click me)</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-footer :fixed="fixed" app>
      <span>&copy; 2017</span>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    data () {
      return {
        clipped: false,
        drawer: true,
        fixed: false,
        items: [
          {
            icon: 'bubble_chart',
            title: 'LOVE',
            route: '/'
          }
        ],
        miniVariant: false,
        right: true,
        rightDrawer: false,
        title: 'Vue me and I vue you'
      }
    },
    methods: {
      goTo (link) {
        this.$router.replace(link || '/')
      }
    },
    computed: {
      menuItems () {
        console.log('Routes ', this.$router.options.routes)
        return this.$router.options.routes.filter(route => {
          return route.redirect === undefined && route.meta.menu === true
        })
      }
    }
  }
</script>



