<template>
  <!--<div class="hello">-->
  <v-container fluid>
    <v-layout>
      <v-flex xs12 sm6 offset-sm3>
        <v-card col12>
          <v-card-title>
            {{ user.username }}
          </v-card-title>
          <v-card-text>
            {{ user.email }}
          </v-card-text>
          <v-card-actions>
            <v-btn class="primary" @click="logout">Log ut</v-btn>
          </v-card-actions>
        </v-card>

      </v-flex>

    </v-layout>
  </v-container>
  <!--</div>-->
</template>

<script>
  import firebase from 'firebase'

  export default {
    name: 'HelloWorld',
    data () {
      return {
        user: {}
      }
    },
    methods: {
//      testAddUserInfo () {
//        let cUser = firebase.auth().currentUser
//        console.log(cUser.uid)
//        let uid = cUser.uid
//        let name = 'WOLFGANG BORN'
//        firebase.database().ref('users/' + uid).set({
//          username: name,
//          email: cUser.email
//        })
//      },
      logout () {
        firebase.auth().signOut().then(
          () => {
            this.$router.replace('login')
          }
        )
      }
    },
    created () {
      let cUser = firebase.auth().currentUser
      firebase.database().ref('/users/' + cUser.uid).once('value').then((snapshot) => {
        this.user = snapshot.val() || 'NOBODY'
      })
    }
//    computed: {
//      getUserInfo () {
//        return this.user
//      }
//    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



