<template>
    <v-card class="login">
      <v-card-text>
        <v-container fluid>
          <v-layout row>
            <v-flex xs4>
              <v-subheader>Navn</v-subheader>
            </v-flex>
            <v-flex xs8>
              <v-text-field
              name="name"
              label="Skriv navnet ditt"
              v-model="user.name"
              >

              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs4>
              <v-subheader>Epost</v-subheader>
            </v-flex>
            <v-flex xs8>
              <v-text-field
                name="email"
                label="Skriv epostadressen din"
                v-model="user.email"
              >
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs4>
              <v-subheader>Passord</v-subheader>
            </v-flex>
            <v-flex xs8>
              <v-text-field
              name="password"
              label="Skriv passordet ditt"
              v-model="user.password"
              min="8"
              :append-icon="v1 ? 'visibility' : 'visibility_off'"
              :append-icon-cb="() => v1 = !v1"
              :type="v1 ? 'password' : 'text'"
              >
              </v-text-field>
            </v-flex>
          </v-layout>

          <v-layout row>
            <v-flex xs4>
              <v-subheader>Bekreft Passord</v-subheader>
            </v-flex>
            <v-flex xs8>
              <v-text-field
              name="password"
              label="Gjenta passordet ditt"
              v-model="user.password2"
              min="8"
              :append-icon="v2 ? 'visibility' : 'visibility_off'"
              :append-icon-cb="() => v2 = !v2"
              :type="v2 ? 'password' : 'text'"
              v-bind:rules="[() => (user.password === user.password2 || 'Passord er ikke like')]"

              >
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout class="text-xs-center" row>
            <v-flex xs12>
              <v-btn @click="signUp" color="primary">Registrer deg</v-btn>
            </v-flex>
          </v-layout>
          <v-layout class="text-xs-center" row>
            <v-flex xs12>
              <v-btn flat color="primary" href="#/login">Eller gå til log inn</v-btn>
              <!--<router-link class="v-btn" to="/login">Eller gå til log inn</router-link>-->
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
    </v-card>
</template>
<script>
  import firebase from 'firebase'
  export default {
    name: 'SignUp',
    data () {
      return {
        v1: true,
        v2: true,
        user: {
          name: '',
          email: '',
          password: '',
          password2: ''
        },
        matchPassword: false
      }
    },
    methods: {
      signUp () {
        firebase.auth().createUserWithEmailAndPassword(this.user.email, this.user.password).then(
          (user) => {
            this.$router.replace('hello')
          },
          (err) => {
            console.log(err)
          }
        )
      }
    }
  }
</script>
<style>
    .login {
    }
</style>



